import React, { MouseEvent } from 'react';
// material-ui
import { makeStyles } from '@material-ui/core/styles';
import {
	Box,
	Button,
	FormControl,
	FormHelperText,
	Grid,
	IconButton,
	InputAdornment,
	InputLabel,
	OutlinedInput,
	Typography,
} from '@material-ui/core';

// third party
import * as Yup from 'yup';
import { Formik } from 'formik';

// project imports
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { useDispatch } from 'react-redux';
import _get from 'lodash.get';
import { useAuth } from '../../../../hooks';
import AnimateButton from '../../../../ui-component/extended/AnimateButton';
import {
	strengthColor,
	strengthIndicator,
} from '../../../../utils/password-strength';
import { SNACKBAR_OPEN } from '../../../../store/actions';

// style constant
const useStyles = makeStyles(theme => ({
	loginInput: {
		...theme.typography.customInput,
	},
	notchedOutline: {
		borderRadius: `${theme?.customization?.borderRadius}px`,
	},
}));

const CompletePasswordForm = ({ ...others }) => {
	const classes = useStyles();
	const [showPassword, setShowPassword] = React.useState(false);
	const [strength, setStrength] = React.useState(0);
	const [level, setLevel] = React.useState<
		{ label: string; color: string } | undefined
	>(undefined);

	const { completePassword } = useAuth();
	const dispatch = useDispatch();

	const handleClickShowPassword = () => {
		setShowPassword(!showPassword);
	};

	const handleMouseDownPassword = (event: MouseEvent) => {
		event.preventDefault();
	};

	const changePassword = (value: string) => {
		const temp = strengthIndicator(value);
		setStrength(temp);
		setLevel(strengthColor(temp) as any);
	};

	return (
		<Formik
			initialValues={{
				password: '',
				confirmPassword: '',
				submit: null,
			}}
			validationSchema={Yup.object().shape({
				password: Yup.string().max(255).required('Password is required'),
				confirmPassword: Yup.string()
					.max(255)
					.required('Confirm password is required')
					.oneOf([Yup.ref('password'), null], 'Both passwords must match!'),
			})}
			onSubmit={async (values, { setStatus, setSubmitting }) => {
				try {
					await completePassword(values.password);
				} catch (err) {
					setStatus({ success: false });
					setSubmitting(false);
					dispatch({
						type: SNACKBAR_OPEN,
						open: true,
						message: _get(err, ['message'], ''),
						variant: 'alert',
						alertSeverity: 'error',
						anchorOrigin: { vertical: 'top', horizontal: 'center' },
					});
				}
			}}
		>
			{({
				errors,
				handleBlur,
				handleChange,
				handleSubmit,
				isSubmitting,
				touched,
				values,
			}) => (
				<form noValidate onSubmit={handleSubmit} {...others}>
					<FormControl
						fullWidth
						error={Boolean(touched.password && errors.password)}
						className={classes.loginInput}
						variant='outlined'
					>
						<InputLabel htmlFor='outlined-adornment-password-reset'>
							Password
						</InputLabel>
						<OutlinedInput
							id='outlined-adornment-password-reset'
							type={showPassword ? 'text' : 'password'}
							value={values.password}
							name='password'
							onBlur={handleBlur}
							onChange={e => {
								handleChange(e);
								changePassword(e.target.value);
							}}
							endAdornment={
								<InputAdornment position='end'>
									<IconButton
										aria-label='toggle password visibility'
										onClick={handleClickShowPassword}
										onMouseDown={handleMouseDownPassword}
										edge='end'
									>
										{showPassword ? <Visibility /> : <VisibilityOff />}
									</IconButton>
								</InputAdornment>
							}
							inputProps={{
								classes: {
									notchedOutline: classes.notchedOutline,
								},
							}}
						/>
					</FormControl>
					{touched.password && errors.password && (
						<FormControl fullWidth>
							<FormHelperText error id='standard-weight-helper-text-reset'>
								{' '}
								{errors.password}{' '}
							</FormHelperText>
						</FormControl>
					)}
					{strength !== 0 && (
						<FormControl fullWidth>
							<Box mb={2}>
								<Grid container spacing={2} alignItems='center'>
									<Grid item>
										<Box width={85} height={8} borderRadius={7} bgcolor={level?.color} />
									</Grid>
									<Grid item>
										<Typography variant='subtitle1' fontSize='0.75rem'>
											{level?.label}
										</Typography>
									</Grid>
								</Grid>
							</Box>
						</FormControl>
					)}

					<FormControl
						fullWidth
						error={Boolean(touched.confirmPassword && errors.confirmPassword)}
						className={classes.loginInput}
						variant='outlined'
					>
						<InputLabel htmlFor='outlined-adornment-confirm-password'>
							Confirm Password
						</InputLabel>
						<OutlinedInput
							id='outlined-adornment-confirm-password'
							type='password'
							value={values.confirmPassword}
							name='confirmPassword'
							label='Confirm Password'
							onBlur={handleBlur}
							onChange={handleChange}
							inputProps={{
								classes: {
									notchedOutline: classes.notchedOutline,
								},
							}}
						/>
					</FormControl>

					{touched.confirmPassword && errors.confirmPassword && (
						<FormControl fullWidth>
							<FormHelperText error id='standard-weight-helper-text-confirm-password'>
								{' '}
								{errors.confirmPassword}{' '}
							</FormHelperText>
						</FormControl>
					)}

					{errors.submit && (
						<Box mt={3}>
							<FormHelperText error>{errors.submit}</FormHelperText>
						</Box>
					)}
					<Box mt={1}>
						<AnimateButton>
							<Button
								disableElevation
								disabled={isSubmitting}
								fullWidth
								size='large'
								type='submit'
								variant='contained'
								color='secondary'
							>
								Update Password
							</Button>
						</AnimateButton>
					</Box>
				</form>
			)}
		</Formik>
	);
};

export default CompletePasswordForm;
